#lastMainView {
	
	
}

button {
	width:100%;
	height:50%;
	border-radius:25px;
	
}

#buttonDivLast {
	position:absolute;
	
	top:50%;
	width:55vh;
	left:50%;
	transform:translate(-50%,-50%);
	z-index:107;
	height:70vh;
	
	text-align:center;

}

#playButton {
	display:inline;
	position:absolute;
	left:0;
	top:0vh;
	box-sizing:border-box;
	z-index:108;
	
	height:20vh;
	
	border:solid 1px black;
	border-radius:12vh;
	background-color:hsl(200,100%,70%);
	font-size:10vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 black;
	outline:none;
	width:55vh;
	left:0;
	line-height:10vh;
	
}

#playButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(5px);
	box-shadow:0 5px 0 0 black;
	color:grey;
}

#portfolioButton {
	display:inline;
	position:absolute;
	left:0;
	top:30vh;
	box-sizing:border-box;
	z-index:108;
	line-height:10vh;
	height:30vh;
	
	border:solid 1px black;
	border-radius:12vh;
	background-color:hsl(200,100%,70%);
	font-size:10vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 black;
	outline:none;
	width:55vh;
	left:0;
	
}

#portfolioButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(5px);
	box-shadow:0 5px 0 0 black;
	color:grey;
}
