body{
	height:100vh;
	width:100vw;
	margin:0;
	overflow:hidden;
	position:fixed;
}

#root {
	height:100%;
	width:100%;
}
#firstMainView {
	background-color:black;
	position:absolute;
	text-align:center;
	width:100vw;
	height:100vh;
	user-select:none;
	z-index:0;
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:104;
	user-select:none;
}

.bug {
	display:inline;
	position:absolute;
	height:10vh;
	width:10vh;
	z-index:101;
	background:none;
	
	user-select:none;
}

#blocker {
	background-color:grey;
	height:100%;
	width:100%;
	position:absolute;
	z-index:106;
	opacity:0.8;
	
}

#easyButton {
	display:inline;
	position:absolute;
	left:0;
	top:0vh;
	box-sizing:border-box;
	z-index:108;
	
	height:20vh;
	
	border:solid 1px black;
	border-radius:12vh;
	background-color:hsl(200,100%,70%);
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 black;
	outline:none;
	width:55vh;
	left:0;
	
}

#easyButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(5px);
	box-shadow:0 5px 0 0 black;
	color:grey;
}

#mediumButton {
	display:inline;
	position:absolute;
	
	top:20vh;
	box-sizing:border-box;
	z-index:108;
	padding:0;
	height:20vh;

	border:solid 1px black;
	border-radius:12vh;
	background-color:hsl(200,100%,50%);
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 black;
	outline:none;
	width:55vh;
	left:0;
	
}

#mediumButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(5px);
	box-shadow:0 5px 0 0 black;
	color:grey;
}




#hardButton {
	display:inline;
	position:absolute;
	
	top:40vh;
	box-sizing:border-box;
	z-index:108;
	padding:0;
	height:20vh;

	border:solid 1px black;
	border-radius:12vh;
	background-color:hsl(200,100%,30%);
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 black;
	outline:none;
	width:55vh;
	left:0;
	
}

#hardButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(5px);
	box-shadow:0 5px 0 0 black;
	color:grey;
}

#buttonDiv {
	position:absolute;
	
	top:50%;
	width:55vh;
	left:50%;
	transform:translate(-50%,-50%);
	z-index:107;
	height:60vh;
	
	text-align:center;

}

#gameAreaZero {
	background-image:linear-gradient(to bottom,hsl(240,90%,65%),hsl(350,10%,80%));
	position:absolute;
	height:100%;
	width:55vh;
	left:50%;
	transform:translate(-50%,0%);
	z-index:100;
}
#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:0;
	user-select:none;
	background:none;
}

/*SmartPhones */
@media only screen and (max-width:600px) {
	
	
	#easyButton {
		position:absolute;
		top:0;
		z-index:107;
		left:0;
		height:20vh;
		width:100%;
		border:solid 1px black;
		border-radius:10vh;
		background-color:hsl(200,100%,70%);
		font-size:6vh;
		
		box-shadow:0 10px 0 0 black;
		outline:none;
	
	}
	
	#mediumButton {
		position:absolute;
		left:0;
		z-index:107;
		top:20vh;
		height:20vh;
		width:100%;
		border:solid 1px black;
		border-radius:10vh;
		background-color:hsl(200,100%,50%);
		font-size:6vh;
		transform:none;
		box-shadow:0 10px 0 0 black;
		outline:none;
	}
	
	#hardButton {
		position:absolute;
		
		z-index:107;
		top:40vh;
		left:0;
		height:20vh;
		width:100%;
		border:solid 1px black;
		border-radius:10vh;
		background-color:hsl(200,100%,30%);
		font-size:6vh;
		transform:none;
		box-shadow:0 10px 0 0 black;
		outline:none;
	}
}


